@if map-deep-get(
  $_CONFIG,
  'componentes',
  'externos',
  'owl-carousel'
)
{
  /* 
 *  Owl Carousel - Animate Plugin
 */
  .owl-carousel {
    .animated {
      -webkit-animation-duration: 1000ms;
      animation-duration: 1000ms;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
    }
    .owl-animated-in {
      z-index: 0;
    }
    .owl-animated-out {
      z-index: 1;
    }
    .fadeOut {
      -webkit-animation-name: fadeOut;
      animation-name: fadeOut;
    }
    .disabled {
      display: none;
    }
  }
  @-webkit-keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  /* 
 * 	Owl Carousel - Auto Height Plugin
 */
  .owl-height {
    -webkit-transition: height 500ms ease-in-out;
    -moz-transition: height 500ms ease-in-out;
    -ms-transition: height 500ms ease-in-out;
    -o-transition: height 500ms ease-in-out;
    transition: height 500ms ease-in-out;
  }
  /* 
 *  Core Owl Carousel CSS File
 */
  .owl-carousel {
    display: none;
    width: 100%;
    -webkit-tap-highlight-color: transparent;
    /* position relative and z-index fix webkit rendering fonts issue */
    position: relative;
    z-index: 1;
    .owl-stage {
      position: relative;
      -ms-touch-action: pan-Y;
      display: flex;
      &:after {
        content: '.';
        display: block;
        clear: both;
        visibility: hidden;
        line-height: 0;
        height: 0;
      }
    }
    .owl-stage-outer {
      position: relative;
      overflow: hidden;
      display: flex;
      height: 100%;
      /* fix for flashing background */
      -webkit-transform: translate3d(0px, 0px, 0px);
    }
    .owl-controls {
      .owl-nav {
        .owl-prev,
        .owl-next {
          cursor: pointer;
          cursor: hand;
          -webkit-user-select: none;
          -khtml-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
        }
      }
      .owl-dot {
        cursor: pointer;
        cursor: hand;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
      }
    }
    &.owl-loaded {
      display: block;
    }
    &.owl-loading {
      opacity: 0;
      display: block;
    }
    &.owl-hidden {
      opacity: 0;
    }
    .owl-refresh .owl-item {
      display: none;
    }
    .owl-item {
      position: relative;
      min-height: 1px;
      float: left;
      -webkit-backface-visibility: hidden;
      -webkit-tap-highlight-color: transparent;
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      height: 100%;
      img {
        display: block;
        // width: 100%;
        -webkit-transform-style: preserve-3d;
      }
    }
    &.owl-text-select-on .owl-item {
      user-select: auto;
    }
    .owl-grab {
      cursor: move;
      cursor: -webkit-grab;
      cursor: -o-grab;
      cursor: -ms-grab;
      cursor: grab;
    }
    &.owl-rtl {
      direction: rtl;
      .owl-item {
        float: right;
      }
    }
  }
  /* No Js */
  .no-js .owl-carousel {
    display: block;
  }
  /* 
 * 	Owl Carousel - Lazy Load Plugin
 */
  .owl-carousel {
    .owl-item {
      .owl-lazy {
        opacity: 0;
        transition: opacity 400ms ease;
      }
      img {
        transform-style: preserve-3d;
      }
    }
    .owl-video-wrapper {
      position: relative;
      height: 100%;
      background: #000;
    }
    .owl-video-play-icon {
      position: absolute;
      height: 80px;
      width: 80px;
      left: 50%;
      top: 50%;
      margin-left: -40px;
      margin-top: -40px;
      background: url('owl.video.play.png') no-repeat;
      cursor: pointer;
      z-index: 1;
      transition: scale 100ms ease;
      &:hover {
        transition: scale(1.3, 1.3);
      }
    }
    .owl-video-playing {
      .owl-video-tn,
      .owl-video-play-icon {
        display: none;
      }
    }
    .owl-video-tn {
      opacity: 0;
      height: 100%;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: contain;
      transition: opacity 400ms ease;
    }
    .owl-video-frame {
      position: relative;
      z-index: 1;
    }
  }
  .owl-prev,
  .owl-next {
    @include posicion(absolute, 0, null, 0, null);
    // transform: translateY(-50%);
    display: flex;
    align-items: center;
    font-size: 5rem;
    color: $secundario;
    // line-height: 10px;
    cursor: pointer;
    @include transicion;
    z-index: 1;
    background: none;
    border: none;
    i {
      display: block;
      line-height: 0.75;
    }
    &:focus {
      outline: 0;
    }
  }
  .owl-prev {
    left: -60px;
  }
  .owl-next {
    right: -60px;
  }
  .owl-dots {
    @include posicion(absolute, null, 0, 20px, 0);
    text-align: center;
  }
  .owl-dot {
    margin: 0 4px;
    display: inline-block;
    span {
      display: block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: 1px solid blanco(1);
      @include transicion;
    }
    &.active {
      span {
        background: blanco(1);
      }
    }
  }
}
